
.uxora-content{
  &-thumb{
    @include max (1199px){
      padding-right: 40px;
    }
    @include max (991px){
      padding-bottom: 40px;
      padding-right: 0;
      text-align: center;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
    &.thumb2{
      text-align: right;
      @include max (1199px){
        padding-left: 40px;
      }
      @include max (991px){
        text-align: center;
        padding-left: 0;
      }
    }
  }
  &-btn{
    ul{
      li{
        display: inline-block;
        margin-bottom: 35px;
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        transition: all 0.4s;
        padding: 19px 0;
        color: var(--heading-color);
        padding: 15px 20px;
        border-radius: 50px;
        margin-right: 10px;
        transition: all 0.4s;
        background-color: var(--light-color2);
        svg{
          margin-right: 10px;
          margin-top: -5px;
        }
        &:hover{
          background-color: var(--heading-color);
          color: var(--light-color);
          path{
            fill: var(--light-color);
            stroke: var(--light-color);
          }
        }
      }
    }
  }
}
