.uxora-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.uxora-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.uxora-section-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.uxora-section-padding4{
    padding: 0 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.uxora-section-padding5{
    padding: 210px 0 130px;
    @include max (991px){
        padding: 150px 100px;
    }
    @include max (767px){
        padding:130px 0 80px;
    }
}
.uxora-section-padding6{
    padding: 130px 0 0;
    @include max (991px){
        padding: 80px 0 0;
    }
    @include max (767px){
        padding:60px 0 0;
    }
}


// section title

.uxora-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-660{
        max-width: 660px;
    }
    &.max-700{
        max-width: 750px;
    }
    &.integration {
        h2{
            color: var(--light-color);
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-755{
            max-width: 755px;
        }
        &.max-width-800{
            max-width: 800px;
        }
        &.max-width-870{
            max-width: 870px;
        }
        &.max-width-1050{
            max-width: 1050px;
        }
    }
    &.title2{
        @include max (991px){
            text-align: left;
        }
    }
}
.uxora-default{
    &-title {
        background-color: #0057FF1A;
        padding: 8px 24px;
        border-radius: 50px;
        max-width: 440px;
        display: flex;
        text-align: center;
        margin: 0 auto 20px;
        p{
            color: var(--blue-color);
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
        }
        img{
          margin-right: 10px;
        }
        &.title2{
          text-align: left;
          margin: 0;
          max-width: 240px;
          margin-bottom: 20px;
        }
        &.title3{
            text-align: left;
            margin: 0;
            max-width: 303px;
            margin-bottom: 20px;
        }
        &.title4{
            margin: 0 auto;
            max-width: 255px;
            margin-bottom: 20px;
        }
        &.title5{
            margin: 0 auto;
            max-width: 170px;
            margin-bottom: 20px;
            background: rgba(245, 244, 239, .1019607843);
            text-align: left;
            margin: 0;
            margin-bottom: 20px;
            p{
                color: var(--light-color);
                font-weight: 600;
                font-size: 16px;
                line-height: 24px;
            }
        }
        &.title6{
            background-color: rgba(0, 87, 255, .1019607843);
            max-width: 185px;
        }
        &.title7{
            max-width: 215px;
        }
        &.title8{
            max-width: 180px;
        }
        &.title9{
            max-width: 290px;
            margin: 0;
            margin-bottom: 24px;
            &.title10{
                max-width: 350px;
            }
            &.title11{
                max-width: 330px;
            }
        }
        &.title10{
            margin: 0 auto;
            max-width: 253px;
            margin-bottom: 20px;
            background-color: #F5F4EF1A;

            p{
                color: var(--light-color);

            }
        }
        &.title11{
            max-width: 350px;
            margin: 0;
            margin-bottom: 24px;
        }
        &.title12{
            max-width: 273px;
            margin: 0;
            margin-bottom: 24px;
        }
      }
    &-data {
      font-size: 21px;
      line-height: 30px;
        &.data2{
           max-width: 880px;
           margin: 0 auto;
        }
    }
    &-content{
        h2{
            padding-bottom: 20px;
        }
        h3{
            padding-bottom: 15px;
        }
        &.content2{
            max-width: 650px;
            h2, p{
                color: var(--light-color);
            }
            @include max (1199px){
                padding-bottom: 40px;
            }
            @include max (767px){
                padding-bottom: 30px;
            }
        }
    }
    &-text {
        font-size: 21px;
        line-height: 30px;
        padding: 0 0 0 140px;
        @include max (1399px){
            padding: 0 0 0 90px;
        }
        @include max (1199px){
            padding: 0;
        }
    }
}
// default content
.pl-0{
    padding-left: 0!important;
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.mt-30{
    margin-top: 30px;
    @include max (767px){
        margin-top: 24px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
