.uxora-cta{
    &-section{
        padding: 130px 0;
        position: relative;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-content{
        max-width: 760px;
        margin: 0 auto;
        text-align: center;
        h2{
            color: var(--heading-color);
            padding-bottom: 20px;
        }
        p{
            color: var(--heading-color);
            font-size: 21px;
            line-height: 30px;
        }
    }
    &-data{
        ul{
            display: flex;
            align-items: center;
            gap: 24px;
            justify-content: center;
            max-width: 690px;
            flex-wrap: wrap;
            margin: 0 auto;
            @include max (767px){
                display: block;
            }
            li{
                display: flex;
                align-items: center;
                gap: 10px;
                font-size: 16px;
                line-height: 24px;
                font-weight: 600;
                color: var(--heading-color);
                @include max (767px){
                    margin-bottom: 15px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    &-btn{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 24px;
        flex-wrap: wrap;
      }
    // end
}
