.uxora-footer{
  &-section{
    background-color: var(--heading-bg);
    padding: 130px 0 0;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
    }
  }

  &-menu{

    h4{
      margin-bottom: 24px;
      color: var(--light-color);
    }
    ul{
      li{
        color: var(--light-color);
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--light-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--light-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      padding-left: 30px;
      @include max (1399px){
        padding-left: 70px;
      }
      @include max (1199px){
        padding-left: 0;
      }
      @include max (767px){
        padding: 0;
        padding-top: 40px;
      }
    }
    &.menu3{
      padding-left: 100px;
      @include max (1399px){
        padding-left: 150px;
      }
      @include max (1199px){
        padding-left: 0px;
        
      }

      @include max (767px){
        padding-left: 0;
        padding-top: 40px;
      }
    }
  }
  &-textarea{
    padding-right: 15px;
    img{
      padding-bottom: 24px;
    }
    p{
      font-size: 21px;
      font-weight: 400;
      line-height: 30px;
      color: var(--light-color);
    }
    @include max (1399px){
      margin-bottom: 50px;
      max-width: 500px;
    }
    @include max (991px){
      margin-bottom: 40px;
    }

   
  }
 
  &-bottom-text{
    text-align: center;
    padding: 35px 0;
    border-top: 1px solid #F5F4EF1A;
    margin-top: 90px;
    @include max (991px){
      margin-top: 80px;
    }
    @include max (767px){
      margin-top: 60px;
    }
    p{
      color: var(--light-color);
      @include max (991px){
        margin-bottom: 15px;
      }
    }
  }
}
.uxora-conatct{
  &-info{
    ul{
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        span{
          font-weight: 600;
        }
        a{
          color: var(--heading-color);
          span{
            font-weight: 600;
          }
        }
      }
    }
  }
}
.pl-100{
  padding-left: 100px;
  @include max (1399px){
    padding-left: 0;
  }
  @include max (1199px){
    padding-left: 0;
  }

}
.pl-41{
  padding-left: 40px;
  @include max (1399px){
    padding-left: 0;
    padding-top: 40px;
  }
  @include max (767px){
    padding-left: 0;
   
  }
}
.pl-42{
  @include max (1199px){
    padding-left: 40px;
  }
  @include max (767px){
    padding-left: 0;
  }
}

.pl-30{
  padding-left: 30px;
  @include max (1199px){
    padding-left: 0;
  }
}