.uxora-pricing{
    &-wrap{
        background-color: var(--light-color);
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        position: relative;
        @include max (991px){
            padding: 30px 20px;
        }
        &.wrap2{
            margin-bottom: 50px;
            @include max (991px){
                margin-bottom: 40px;
            }
            @include max (767px){
                margin-bottom: 30px;
            }
        }
    }
    &-icon{
        padding-bottom: 24px;
    }
    &-header{
        padding-bottom: 30px;
        border-bottom: 1px solid #0000001A;
        margin-bottom: 30px;
        img{
            padding-bottom: 15px;
        }
        h3{
            padding-bottom: 24px;
        }
    }
    &-body{
        ul{
            li{
                font-weight: 600;
                padding-bottom: 15px;
                position: relative;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    margin-right: 10px;
                }
            }
        }
    }
    &-bottom-text{
        text-align: center;
    }
}
.pr-24{
    padding-right: 24px;
    h2,p{
        color: var(--light-color);
    }
}
.ux-price-value.dynamic-value {
    font-family: $heading-font;
    font-size: 80px;
    font-weight: 700;
    line-height: 90px;
    letter-spacing: -1px;
}

.toggle-btn{
    @include max (767px){
        padding: 10px 0 10px 10px;
    }
}

.pricing-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    @include max (767px){
        display: block;
    }
    label {
        margin: 0 20px;
        font-size: 18px;
        line-height: 30px;
        color: var(--heading-color);
        font-weight: 600;
        @include max(767px) {
        margin: 0 10px;
        font-size: 16px;
        }
        span{
            font-weight: 600;
        }
    }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] {
  background: #ff5722;
  }
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    border: none !important;
    background-position: center right 5px;
    background-color: var(--heading-color);
    background-image: url(../images/pricing/shape.svg) !important;
}
 
.form-check-input:focus {
    border-color: none;
    outline: 0;
    box-shadow:none;
}
.pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/pricing/shape.svg) !important;
}



// add table


tr { 
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @include max (1280px){
        padding: 20px;
    }
    span{
        font-weight: 400;
    }
}

.uxora-pricing-table {
    
    @include max(1349px) {
        overflow-x: auto;
    }
}
.sub-title{
    font-family: $heading-font;
    font-size: 28px;
    font-weight: 600;
    line-height: 32px;
    letter-spacing: -1px;
    color: var(--heading-color);
}

.sub-title2{
    font-family: $heading-font;
    font-size: 80px;
    line-height: 1.1em;
    letter-spacing: -3px;
    font-weight: 600;
    @include max (1199px){
        font-size: 60px;
    }
    @include max (767px){
        font-size: 42px;
    }
}
tr.table-sub-title.title2 {
    max-width: 1260px;
    margin: 0 auto 0 90px;
    padding: 30px 0;
    @include max (991px){
        max-width: 100%;
        margin: 0;
        padding: 20px!important;
    }
}




.table-header{
    .title{
        font-family: $heading-font;
        font-size: 80px;
        font-weight: 600;
        line-height: 80px;
        letter-spacing: -1px;
        color: var(--heading-color);
        padding-bottom: 0;
        @include max (1199px){
            font-size: 48px;
        }
        @include max (767px){
            font-size: 36px;
        }
    }
}

th{
   text-align: center;
    p{
        font-weight: 700;
        text-align: left;
        font-size: 15px;
    }
}
tr{
    @include max (991px){
        padding: 0!important;
    }
}


.divided{
    border-bottom: 1px solid #1405331A;
}