.uxora-video-thumb{
    position: relative;
}
  
  
.play-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include max (767px){
        top: 42%;
    }
    img{
        @include max (991px){
            width: 60px;
        }
    }
}

  
  
  

@keyframes animName {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
  
.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: var(--heading-bg);
    opacity: 0;
    border-radius: 100%;
    left: -55px;
    top: -55px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
    width: 160px;
    height: 160px;
    left: -50px;
    top: -50px;
    }
    &.wave-1 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
    }
    &.wave-2 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
    }
    &.wave-3 {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
    }
    
}
 
    
    
@keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }

    50% {
        opacity: 0.9;
    }

    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}


  


  