input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
   height: 145px;
   resize: none;
   border-radius: 20px;
   background-color: var(--light-color);
   padding: 20px;
   border: 2px solid var(--heading-color);
   margin-bottom: 24px;
   @include max (767px){
      margin-bottom: 20px;
   }
   &::placeholder{
      color: var(--heading-color)!important;
   }
}


.uxora-main-field {
   position: relative;
   margin-bottom: 24px;
   input {
      border-radius: 50px;
      padding: 18px 30% 18px 24px;
      max-width: 500px;
      border: 2px solid var(--heading-color);
      @include max (991px){
         padding: 18px 33% 18px 24px;
      }
      @include max (767px){
         padding: 18px 24px 18px 24px;
      }
      &::placeholder{
         color: var(--heading-color)!important;
      }
   }
   &.field2{
      input{
         max-width: 100%;
         padding: 18px 24px;
      }
   }
}
.sub-btn{
   position: absolute!important;
   top: 5px;
   right: 135px;
   padding: 13px 22px!important;
   width: 215px!important;
   height: 50px;
   transition: all .4s;
   @include max (991px){
      right: 105px!important;
   }
   @include max (767px){
      position: static!important;
      width: 100%!important;
      margin-top: 15px;
      justify-content: center;
   }
}