// fonts variable
$body-font: "Inter", serif!default;
$heading-font: "Phudu", serif!default;
// color variable
:root {
  --heading-color: #000;
  --heading-bg: #000;
  --accent-color: #FF2214;
  --accent-bg: #FF2214;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --light-color2: #EAE4D7;
  --light-color: #F5F4EF;
  --blue-color: #0057FF;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 1.5em;
$buton_border_radius: 50px;
$button_padding: 18px 25px;
$button_font_weight: 700;
$button_font_family: $body-font;