
/* offcanvas menu */
.main-menu {
  display: block; 
}

.uxora-offcanves-sidebar {
  width: 750px;
    display: block;
    background: #ffffff;
    height: 100%;
    position: fixed;
    z-index: 111;
    padding: 80px 40px 60px;
    margin-right: -750px;
    transition: 0.5s;
    right: 0;
    top: 0;
    overflow-y: scroll;
}
.uxora-offcanves-sidebar.active {
      margin-right: 0px;
      z-index: 1111;
}

.close {
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content:center;
      transition: all linear 0.3s;
      cursor: pointer;
      background-color: var(--accent-color);
}


.offcanvas-overlay {
      width: 100%;
      height: 100%;
      background: #000;
      position: fixed;
      z-index: 0;
      visibility: hidden;
      opacity: 0;
      transition: .5s;
 }

.offcanvas-overlay.active {
      opacity: .3;
      z-index: 111;
      visibility: visible;
 }

.menu-bar {
  top: 50%;
  right: 0;
  z-index: 9;
  position: fixed;
  transform: translateY(-50%);
  display: block;
  font-size: 18px;
  cursor: pointer;
  background-color: #fff;
  line-height: 1;
  padding: 15px 20px;
  text-transform: uppercase;
  color: var(--accent-color);
  border-radius: 5px 0 0 5px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 50px;
  @include max(991px) {
    display: none;
  }
}

.uxora-demos-wrap {
  border-radius: 10px;
  margin-bottom: 40px;
  transition: all 0.4s;
  overflow: hidden;
}
.uxora-demos-wrap:hover{
  transform: translateY(-10px);
}
.uxora-demos-item{
  position: relative;
  padding: 10px 10px 0;
  background-color: #fff;
  box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.1);

}
h3.uxora-sidemenu-title {
  text-align: center;
  margin-bottom: 30px;
}
.uxora-demo-title {
  text-align: center;
  margin-top: 30px;
}
.uxora-demo-title h3{
  font-size: 20px;
  margin: 0;
}


.uxora-offcanves-sidebar::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

.uxora-offcanves-sidebar::-webkit-scrollbar{
  width: 5px;
  background-color: #F5F5F5;
}

.uxora-offcanves-sidebar::-webkit-scrollbar-thumb{
  background-color: #000000;
  border: 2px solid #555555;
}

/* offcanvas menu */