.uxora-contact{
    &-box{
        padding: 30px;
        border-radius: 20px;
        border: 2px solid var(--heading-bg);
        background-color: var(--light-color);
        @include max (767px){
            padding: 20px;
        }
        p{
            font-weight: 700;
            margin-bottom: 10px;
        }
    }
    &-info-box{
        margin-bottom: 50px;
        @include max (991px){
            margin-bottom: 40px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
        ul{
            li{
                border-radius: 20px;
                display: flex;
                align-items: center;
                gap: 24px;
                padding: 30px;
                background-color: var(--light-color);
                border: 2px solid var(--heading-color);
                @include max (767px){
                    padding: 20px;
                }
                a{
                    display: flex;
                    align-items: center;
                    gap: 24px;
                }
            }
        }
        &.box2{
            ul{
                li{
                    padding: 34px 30px;
                }
            }
        }
    }
    &-form{
        h3{
            padding-bottom: 30px;
        }
    }
    &-map {
        height: 715px;
        border: 2px solid #000;
        border-radius: 20px;
        width: 500px;
        @include max (1199px){
            width: 400px;
        }
        @include max (991px){
            width: 100%;
            height: 500px;
            margin-bottom: 40px;
        }
        @include max (991px){
            height: 400px;
            margin-bottom: 30px;
        }
    }
}