.uxora-t{
    &-wrap{
        padding: 40px 24px;
        border-radius: 10px;
        margin: 0 12px;
        background-color: var(--light-color);
        @include max (767px){
            margin: 0;
        }
    }
    &-rating{
        padding-bottom: 24px;
    }
    &-data{
        p{
            font-weight: 600;
            font-style: italic;
            font-size: 18px;
            line-height: 30px;
            letter-spacing: 0px;
        }
    }
    &-thumb{
        margin: 24px 0;
        border-radius: 5px;
        background-color: #EAE4D7;
    }
    &-author{
        h4{
            margin-bottom: 5px;
        }
    }
}
.uxora-t-slider-js-init .uxora-t-next, 
.uxora-t-slider-js-init .uxora-t-prev {
    left: -104%;
    width: 50px;
    height: 50px;
    bottom: 10%;
    z-index: 9;
    display: block;
    cursor: pointer;
    position: absolute;
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    transition: all .4s;
    background-image: url(../images/testimonial/arrow.svg);
    border: none;
    background-color: var(--light-color);
    border-radius: 100px;
    &:hover{
        background-color: var(--accent-bg);
    }
    @include max (1499px){
        bottom: 13%;
    }
    @include max(1199px) {
        display: none!important;
    }
  
}
.uxora-t-slider-js-init .uxora-t-prev {
    left: auto;
    right: 189%;
    background-image: url(../images/testimonial/arrow.svg);
    background-color: var(--light-color);
    transform: rotate(180deg);
    @include max (1699px){
        right: 185%;
    }
}

.uxora_screenfix_right {
    margin-right: calc((-100vw + 100%) / 1);
    max-width: 1640px;
    
    @include max (991px){
        max-width: 100%;
        margin: auto;
    }
}
