h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $heading-font;
  color: var(--heading-color);
  font-weight: 600;
  margin: 0;
}
h1,
.h1 {
  font-size: 120px;
  line-height: 1em;
  @include max(1199px) { 
    font-size: 90px;
  }
  @include max(991px) { 
    font-size: 72px;
  }
  @include max(767px) { 
    font-size: 48px;
  }

  @include max(575px) { 
    font-size: 40px;
  }

}

h2,
.h2 {
  font-size: 80px;
  line-height: 1.1em; /* 116.667% */
  @include max (1399px){
    font-size: 55px;
  }
  @include max (991px){
    font-size: 48px;
  }
  @include max (767px){
    font-size: 36px;
  }
}
h3,
.h3 {
  font-size: 30px;
  line-height: 1.17em;
  @include max (767px){
    font-size: 27px;
  }
  
}
h4,
.h4 {
  font-size: 24px;
  line-height: 1.17em;
  font-weight: 700;
  @include max (767px){
    font-size: 20px;
  }
  
}
h5,
.h5 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 700;
}


p{  
  &:last-child{
    margin-bottom: 0;
  }
}


@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}


