
.uxora-social{
    &-wrap{
        transition: all 0.4s;
        ul{
            li{
                display: inline-block;
                margin-left: 10px;
                &:first-child{
                    margin-left: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    justify-content: center;
                    transition: all 0.4s;
                    border: 1px solid var(--light-color);
                    &:hover{
                        border: 1px solid transparent;
                        background-color: var(--accent-bg);
                        path{
                            fill: var(--light-color);
                        }
                    }
                }
            }
        }
    }
  
}
