.uxora-faq{
  &-wrap1{
    padding-left: 70px;
    @include max (1199px){
      padding-left: 20px;
    }
    @include max (991px){
      padding-left: 0;
      padding-top: 40px;
    }
    @include max (767px){
      padding-top: 30px;
    }
  }
  &-header{
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: space-between;
  }
  &-item{
    cursor: pointer;
    padding-bottom: 30px;
    border-bottom: 1px solid #0000001A;
    margin-bottom: 30px;
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }
    &.open {
      position: relative;
      .uxora-faq-header{
        padding-bottom: 15px;
      }
      .uxora-faq-body{
        display: block;
      }
    }
    &.item2{
      padding: 0 0 0 20px;
      background-color: var(--light-color);
      &.open{
        &::after{
          opacity: 1;
        }
      }
      &::before{
        width: 4px;
        height: 100%;
        border-radius: 7px;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        background-color: #14053333;
      }
      &::after{
        width: 4px;
        height: 100%;
        border-radius: 0;
        top: 0;
        left: 0;
        content: "";
        position: absolute;
        opacity: 0;
        background-color: var(--heading-color);
      }
    }
  }
  &-body{
    display: none;
  }
}
img.plasicon{
  transition: all 0.4s;
}
.uxora-faq-item.open {
  img.plasicon{
    transform: rotate(45deg);
  }
}
.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}




// add tab

.tabs{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
  padding-bottom: 80px;
  @include max (991px){
    padding-bottom: 50px;
  }
  @include max (767px){
    padding-bottom: 30px;
  }
  li{
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    padding: 18px 24px;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.4s;
    border: 2px solid var(--heading-color);
    &.current{
      border: 2px solid var(--accent-color);
      color: var(--light-color);
      background-color: var(--accent-color);
    }
  }
}


.uxora-tab-content{
	display: none;
}

.uxora-tab-content.current{
	display: inherit;
}