.uxora-hero{
  &-section{
    padding: 210px 0 0;
    position: relative;
    @include max (991px){
      padding: 190px 0 0;
    }
    @include max (767px){
      padding: 150px 0 0;
    }
    @include max (470px){
      padding: 130px 0 0;
    }
  }
  &-thumb{
    padding-top: 50px;
    img{
      width: 430px;
      @include max (1399px){
        width: 365px;
      }
      @include max (1399px){
        width: 305px;
      }
      @include max (991px){
        width: 225px;
      }
      @include max (767px){
        width: 100%;
      }
      &.card0{
        @include max (767px){
          display: none;
        }
      }
    }
    @include max (767px){
      padding-top: 30px;
    }
  }
  &-content{
    max-width: 1000px;
    text-align: center;
    margin: 0 auto;
    p{
      font-size: 21px;
      line-height: 30px;
    }
  }
  &-btn{
    display: flex;
    align-items: center;
    gap: 24px;
    flex-wrap: wrap;
    justify-content: center;
  }
  &-card{
    position: absolute;
    bottom: 215px;
    left: 115px;
    @include max (1799px){
      display: none;
    }
  }
  &-card2{
    position: absolute;
    bottom: 190px;
    right: 60px;
    @include max (1799px){
      display: none;
    }
  }
}
